html, body {
	color: #242729;
	font-family: Arial,"Helvetica Neue",Helvetica,sans-serif;
	font-size: 13px;
	line-height: 1.30769231;
}

body {
	padding-top: 50px;
}

ul, li, p,
h1, h2, h3, h4, h5, h6 {
	margin: 0;
	padding: 0;
}

ul, li {
	display: block;
  list-style: none;
}

a:hover,
a:active {
	color: #0095ff;
	text-decoration: none;
}

h1 {
	font-size: 27px;
	margin-bottom: 20px;
}

button {
	border: none;
	background: none;
}